  background-color: #f6f6f6;
  margin: 0;
  padding: 0;
  font-family: DM Sans;
}

.container {
  max-width: 375px;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
