#toggle {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  height: 20px;
  width: 40px;
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 20px;
  outline: none;
  position: relative;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

@media (min-width: 1440px) {
  #toggle {
    margin-left: 745px;
    margin-right: 24px;
  }
}

#toggle:after {
  content: "";
  height: 18px;
  width: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  position: absolute;
  top: 1px;
  left: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

