.k-modal {
  z-index: 100;
  letter-spacing: -.01em;
  text-transform: uppercase;
  color: #111;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  background-color: #4f2ee8;
  justify-content: center;
  align-items: center;
  padding: 16px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.33;
  transition: all .3s;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
