  content: "";
  width: 1421px;
  height: 1421px;
  z-index: 0;
  filter: blur(539.791px);
  background: rgba(204, 236, 231, .5);
  position: absolute;
  top: -918px;
  left: -846px;
}

.k-modal__form {
  z-index: 1;
