.button-up {
  height: 64px;
  width: 64px;
  background-color: rgba(79, 46, 232, .3);
  border: 0;
  border-radius: 50%;
  padding: 5px;
  animation: button-animation 1s infinite;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

@keyframes button-animation {
  0% {
    padding: 5px;
